import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Avatar from '@mui/material/Avatar';

const dispatchedEvents = [];

const SrsMuiAvatar = function (props, ref) {
  let componentProps = {...props, theme: props.muiTheme};
  return (
    <Avatar {...componentProps}>
      <slot />
      {props.content}
    </Avatar>
  );
}

const styles = `
  /* Custom styles for the Avatar component */
`;

const props = [
  {
    name: "alt",
    defaultValue: "",
    type: "string",
  },
  {
    name: "src",
    defaultValue: "",
    type: "string",
  },
  {
    name: "srcSet",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sizes",
    defaultValue: "",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "circular",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "default",
    type: "string",
  },
  {
    name: "content",
    defaultValue: null,
    type: "object",
  },
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object",
  },
  // Add other props specific to Avatar if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiAvatar), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
